import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Chip from "@mui/material/Chip";

const dispatchedEvents = [];

const SrsMuiChip = function (props, ref) {
  return (
    <Chip
      label={props.label}
      color={props.color}
      variant={props.variant}
      disabled={props.disabled}
      clickable={props.clickable}
      onClick={props.onClick}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiChip component */
`;

const props = [
  {
    name: "label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "default",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "filled",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "clickable",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiChip if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiChip),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
